import {
  AfterContentChecked,
  ChangeDetectorRef,
  Component, EventEmitter,
  Input,
  OnChanges,
  OnInit, Output,
  SimpleChanges
} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';

declare var $bean;
declare var $;

@Component({
  selector: 'box-activities-messenger',
  templateUrl: './box-activities-messenger.component.html',
  styleUrls: ['./box-activities-messenger.component.css']
})
export class BoxActivitiesMessengerComponent implements OnInit, OnChanges {

  @Input() setUpActivitiesObj: any;
  @Input() selectedMessenger: any;
  @Input() styleBoxMessageActivities: any;
  @Output() updateMessenger: EventEmitter<any> = new EventEmitter();
  @Output() forwardMessenger: EventEmitter<any> = new EventEmitter();
  @Output() copyMessenger: EventEmitter<any> = new EventEmitter();
  @Output() removeMessenger: EventEmitter<any> = new EventEmitter();

  constructor(private cdRef: ChangeDetectorRef) {
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  beginUpdateMessenger() {
    this.updateMessenger.emit(this.selectedMessenger);
  }

  beginForwardMessenger() {
    this.forwardMessenger.emit(this.selectedMessenger);
  }

  beginCopyMessenger() {
    this.copyMessenger.emit(this.selectedMessenger);
  }

  beginRemoveMessenger() {
    this.removeMessenger.emit(this.removeMessenger);
  }

  setUpActivites(messenger) {
    if ($bean.isNotNil(messenger.path)) {
      this.setUpActivitiesObj.download = true;
    }
  }
}
