import {Component, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {GlobalService} from "../../common/services/global.service";

declare var $bean;
declare var $;

@Component({
  selector: 'modal-forward-messenger',
  templateUrl: './modal-forward-messenger.html',
  styleUrls: ['./modal-forward-messenger.css']
})
export class ModalForwardMessenger implements OnInit {

  // baseUrl = "https://chat.hyper.com:9999/";
  baseUrl = "http://chat.hyper.com:9000/";
  TYPE_MESSENGER_TEXT = 'TEXT';
  TYPE_MESSENGER_IMAGE = 'IMAGE';
  TYPE_MESSENGER_LINK = 'LINK';
  TYPE_MESSENGER_FILE = 'FILE';
  suggestedChats: any = [];
  availbleChats: number = 0;
  cloneSuggestedChats: any = [];
  messenger: any = [];
  user: any = {};
  noteAttached: any = '';
  searchValue: string = '';
  bean = $bean;

  constructor(private http: HttpClient, private globalService: GlobalService, private router: Router) {
  }

  ngOnInit() {
    this.globalService.hasForwardMessenger.subscribe(res => {
      if ($bean.isNotEmpty(res)) {
        this.messenger = res;
        this.user = this.messenger['user'];
        this.noteAttached = '';
        this.searchValue = '';
        this.cloneSuggestedChats = $bean.clone(this.suggestedChats);
      }
    })
    this.globalService.suggestedChats.subscribe(res => {
      if ($bean.isNotEmpty(res)) {
        this.suggestedChats = res['suggestedChats'];
        this.availbleChats = res['availbleChats'];
        this.searchChats(this.searchValue);
      }
    })
  }

  sendMessenger(messenger, channelId) {
    let cloneMessenger = $bean.clone(messenger);
    let $this = this;
    delete cloneMessenger['id'];
    let url = this.baseUrl + 'messengers/insert';
    cloneMessenger['channelId'] = channelId;
    this.http.post(url, cloneMessenger).subscribe(data => {
      if ($bean.isNotEmpty(data) && $bean.isNotEmpty(this.cloneSuggestedChats)) {
        for (let i = 0; i < this.cloneSuggestedChats.length; i++) {
          if (data['channelId'] == this.cloneSuggestedChats[i].id) {
            this.cloneSuggestedChats[i]['isSent'] = true;
            $this.sendAttachedMessenger(channelId);
          }
        }
      }
    })
  }

  sendAttachedMessenger(channelId) {
    if ($bean.isNotEmpty(this.noteAttached)) {
      let url = this.baseUrl + 'messengers/insert';
      let attachedMessenger = {
        message: this.noteAttached,
        channelId: channelId
      }
      this.http.post(url, attachedMessenger).subscribe(data => {
        console.log('Insert Attached');
        console.log(data);
      })
    }
  }

  linkExternalFile(path) {
    let url = this.baseUrl + path;
    window.open(url, "_blank");
  }

  searchChats(value) {
    this.cloneSuggestedChats = [];
    this.searchValue = value;
    if ($bean.isNotEmpty(this.suggestedChats)) {
      for (var i = 0; i < this.suggestedChats.length; i++) {
        if ($bean.textContains(this.suggestedChats[i]['title'], this.searchValue, true)) {
          this.cloneSuggestedChats.push(this.suggestedChats[i]);
        }
      }
    }
  }

  moreChats() {
    this.globalService.getMoreChats.next(true);
  }
}
