import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {GlobalService} from "../../common/services/global.service";

declare var $bean;

@Component({
  selector: 'box-view-contacts',
  templateUrl: './box-view-contacts.component.html',
  styleUrls: ['./box-view-contacts.component.css']
})
export class BoxViewContactsComponent implements OnInit {
  // baseUrl = "https://chat.hyper.com:9999/";
  baseUrl = "http://chat.hyper.com:9000/";
  @Input() suggestedContacts: any;
  @Input() availbleContacts: any;
  @Output() moreContacts: EventEmitter<any> = new EventEmitter();

  constructor(private http: HttpClient, private globalService: GlobalService, private router: Router) {
  }

  ngOnInit() {
    if($bean.isNil(this.suggestedContacts)){
      this.suggestedContacts = [];
    }
    if($bean.isNil(this.availbleContacts)){
      this.availbleContacts = 0;
    }
  }

  chooseContact(contact) {
    this.globalService.accessChat.next(contact);
  }

  getMoreContacts() {
    this.moreContacts.emit(null);
  }
}
