import {ChangeDetectorRef, Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {GlobalService} from "../../common/services/global.service";

declare var $bean;
declare var $;

@Component({
  selector: 'box-read-messenger',
  templateUrl: './box-read-messenger.component.html',
  styleUrls: ['./box-read-messenger.component.css']
})
export class BoxReadMessengerComponent implements OnInit, OnChanges {

  @Input() userChannels: any;
  @Input() messengerId: any;
  @Input() isViewUsersRead: any;
  LENGTH_SHOW_USER_SAMPLE = 3;
  listUserRead: any = [];
  listUserReadSample: any = [];
  randomeId = $bean.genRandomID(16);

  constructor(private globalService: GlobalService, private cdRef: ChangeDetectorRef) {
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.setUpBox();
  }

  ngOnInit() {
    if ($bean.isNil(this.userChannels)) {
      this.userChannels = [];
    }
    if ($bean.isNil(this.messengerId)) {
      this.messengerId = '';
    }
    if ($bean.isNil(this.isViewUsersRead)) {
      this.isViewUsersRead = true;
    }
    this.setUpBox();
  }


  setUpBox() {
    this.listUserRead = [];
    this.listUserReadSample = [];
    for (let i = 0; i < this.userChannels.length; i++) {
      if (this.userChannels[i].lastMessengerId == this.messengerId) {
        this.listUserRead.push(this.userChannels[i]);
      }
    }
    if ($bean.isNotEmpty(this.listUserRead)) {
      let lengthSampleUser = this.LENGTH_SHOW_USER_SAMPLE;
      if (this.listUserRead.length < this.LENGTH_SHOW_USER_SAMPLE) {
        lengthSampleUser = this.listUserRead.length;
      }
      for (let i = 0; i < lengthSampleUser; i++) {
        this.listUserReadSample.push(this.listUserRead[i]);
      }
    }
  }

  showBoxUsersRead(event, triggerElementId) {
    if ($bean.isNotEmpty(this.listUserRead)) {
      let usersRead = [];
      for (let i = 0; i < this.listUserRead.length; i++) {
        usersRead.push(this.listUserRead[i]['user']);
      }
      if ($bean.isNotEmpty(usersRead)) {
        this.globalService.dataShowUsersReadMessenger.next({
          readUsers: usersRead,
          triggerElementId: triggerElementId
        })
        event.stopPropagation();
      }
    }
  }
}
