import {
  AfterContentChecked,
  ChangeDetectorRef,
  Component, EventEmitter,
  Input,
  OnChanges,
  OnInit, Output,
  SimpleChanges
} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';

declare var $bean;
declare var $;

@Component({
  selector: 'box-info-user',
  templateUrl: './box-info-user.component.html',
  styleUrls: ['./box-info-user.component.css']
})
export class BoxInfoUserComponent implements OnInit, OnChanges {

  @Input() user: any;
  @Output() callBack: EventEmitter<any> = new EventEmitter();

  constructor(private cdRef: ChangeDetectorRef) {
  }

  ngOnInit() {
    if ($bean.isNil(this.user)) {
      this.user = {};
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  chooseUser() {
    this.callBack.emit(this.user);
  }
}
