import {
  AfterContentChecked,
  ChangeDetectorRef,
  Component, EventEmitter,
  Input,
  OnChanges,
  OnInit, Output,
  SimpleChanges
} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {EmojiEvent} from "@ctrl/ngx-emoji-mart/ngx-emoji";

declare var $bean;
declare var $;

@Component({
  selector: 'box-emojis',
  templateUrl: './box-emojis.component.html',
  styleUrls: ['./box-emojis.component.css']
})
export class BoxEmojisComponent implements OnInit, OnChanges {

  @Input() styleBoxMessageEmojis: any;
  @Output() chooseEmoji: EventEmitter<any> = new EventEmitter();

  constructor(private cdRef: ChangeDetectorRef) {
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  handleClick(evt: EmojiEvent) {
    console.log(evt.emoji);
    let selectedEmoji = evt.emoji;
    this.chooseEmoji.emit(selectedEmoji.native);
  }

}
