import {
  ChangeDetectorRef,
  Component, EventEmitter, HostListener,
  Input,
  OnChanges,
  OnInit, Output,
} from '@angular/core';

declare var $bean;
declare var $;

@Component({
  selector: 'box-input-search',
  templateUrl: './box-input-search.component.html',
  styleUrls: ['./box-input-search.component.css']
})

export class BoxInputSearchComponent implements OnInit {

  @Input() unFocusPlaceholder: string;
  @Input() focusPlaceholder: string;
  @Output() callBack: EventEmitter<any> = new EventEmitter();
  searchValue: string;
  isFocusSearch = false;
  idInputSearch = 'input-search-' + $bean.genRandomID(16);

  constructor() {

  }

  ngOnInit() {
    if($bean.isNil(this.unFocusPlaceholder)){
      this.unFocusPlaceholder = 'Tìm kiếm thông tin';
    }

    if($bean.isNil(this.focusPlaceholder)){
      this.focusPlaceholder = 'Tìm kiếm';
    }
  }

  closeSearch() {
    this.isFocusSearch = false;
    this.searchValue = '';
  }

  triggerSearch() {
    this.isFocusSearch = true;
    $('#' + this.idInputSearch).focus();
  }

  search() {
    this.callBack.emit(this.searchValue);
  }
}
