import {
  AfterContentChecked,
  ChangeDetectorRef,
  Component, EventEmitter,
  Input,
  OnChanges,
  OnInit, Output,
  SimpleChanges
} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {GlobalService} from "../../common/services/global.service";

declare var $bean;
declare var $;

@Component({
  selector: 'box-header-chat',
  templateUrl: './box-header-chat.component.html',
  styleUrls: ['./box-header-chat.component.css']
})
export class BoxHeaderChatComponent implements OnInit, OnChanges {
  @Input() userLogin: any;
  selectedChat: any = {};
  requestChat: any = {};
  // baseUrl = "https://chat.hyper.com:9999/";
  baseUrl = "http://chat.hyper.com:9000/";
  ROLE_IS_CHANNEL = 'CHANNEL';
  ROLE_IS_USER = 'USER';
  countMembers: number = 0;
  infoChat: any = {};
  bean = $bean;

  constructor(private cdRef: ChangeDetectorRef, private http: HttpClient, private globalService: GlobalService, private router: Router) {
  }

  ngOnInit() {
    this.globalService.changeFocusChat.subscribe(res => {
      if ($bean.isNotEmpty(res)) {
        this.selectedChat = res['selectedChat'];
        this.requestChat = res['requestChat'];
        if (this.selectedChat['role'] == this.ROLE_IS_CHANNEL) {
          this.getNumberMembers(this.requestChat['channelId']);
        }
      } else {
        this.selectedChat = {};
        this.requestChat = {};
      }
    })

    this.globalService.hasNewUserChannel.subscribe(res => {
      this.notifiedNewUserChannel(res);
    });

    this.globalService.hasDeletedUserChannel.subscribe(res => {
      this.notifiedDeleteUserChannel(res);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  showInfoChat() {
    if (this.selectedChat['role'] == this.ROLE_IS_CHANNEL) {
      this.getInfoDetailChannel();
    } else {
      this.getInfoDetailContact();
    }
  }

  showGalleryBox() {
    $('#galleryModalBox').modal('show');
  }

  addMoreUser() {
    if (this.selectedChat['role'] == this.ROLE_IS_CHANNEL) {
      this.addUserToChat();
    } else {
      this.addNewChannel();
    }
  }

  addUserToChat() {
    $('#modalDetailAddUserToChat').modal('show');
    // reset lại giá trị của đối tượng newChannel
    this.globalService.addUserToChat.next({channelId: this.requestChat['channelId']});
  }

  addNewChannel() {
    $('#modalInitAddChannel').modal('show');
    // reset lại giá trị của đối tượng newChannel
    this.globalService.addChannel.next({members: [this.selectedChat['id']]});
  }

  getInfoDetailChannel() {
    $('#modalBoxChat').modal('show');
    this.getInfoChat(this.requestChat['channelId']);
  }

  getInfoDetailContact() {
    $('#modalBoxContact').modal('show');
    // reset lại giá trị của đối tượng newChannel
    this.globalService.infoContact.next({infoContact: this.selectedChat, linkUserChannel: this.requestChat});
  }

  getInfoChat(channelId) {
    let url = this.baseUrl + 'channels/infoChannel';
    let form = {
      channelId: channelId
    }
    this.http.post(url, form).subscribe(channel => {
      if ($bean.isNotEmpty(channel)) {
        this.infoChat = channel;
        if ($bean.isNotEmpty(channel['users'])) {
          this.countMembers = channel['users'].length;
        }
        this.globalService.infoChat.next({infoChat: this.infoChat, linkUserChannel: this.requestChat});
      }
    })
  }

  getNumberMembers(channelId) {
    let url = this.baseUrl + 'channels/infoChannel';
    let form = {
      channelId: channelId
    }
    this.http.post(url, form).subscribe(channel => {
      if ($bean.isNotEmpty(channel)) {
        if ($bean.isNotEmpty(channel['users'])) {
          this.countMembers = channel['users'].length;
        }
      }
    })
  }

  showInfoChannel() {
    if (this.selectedChat['role'] == this.ROLE_IS_CHANNEL) {
      this.getInfoDetailChannel();
    } else {
      this.getInfoDetailContact();
    }
  }

  notifiedNewUserChannel(data) {
    if ($bean.isNotEmpty(data) && (data['value']['channelId'] == this.requestChat['channelId'])) {
      this.countMembers++;
    }
  }

  notifiedDeleteUserChannel(data) {
    if ($bean.isNotEmpty(data) && (data['channelId'] == this.requestChat['channelId']) && (data['value']['userId'] == this.requestChat['userId'])) {
      this.requestChat = {};
    } else if ($bean.isNotEmpty(data) && (data['value']['channelId'] == this.requestChat['channelId'])) {
      this.countMembers--;
    }
  }
}
