import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Router} from "@angular/router";
import {GlobalService} from "../../common/services/global.service";

declare var $bean;

@Component({
  selector: 'box-view-chats',
  templateUrl: './box-view-chats.component.html',
  styleUrls: ['./box-view-chats.component.css']
})
export class BoxViewChatsComponent implements OnInit {
  bean = $bean;
  @Input() suggestedChats: any;
  @Input() availbleChats: any;
  @Input() statusUserChannels: any;
  @Output() moreChats: EventEmitter<any> = new EventEmitter();

  constructor(private http: HttpClient, private globalService: GlobalService, private router: Router) {
  }

  ngOnInit() {
    if ($bean.isNil(this.suggestedChats)) {
      this.suggestedChats = [];
    }
    if ($bean.isNil(this.statusUserChannels)) {
      this.statusUserChannels = {};
    }
    if ($bean.isNil(this.availbleChats)) {
      this.availbleChats = 0;
    }
  }

  chooseChat(chat) {
    this.globalService.accessChat.next(chat);
  }

  getMoreChats() {
    this.moreChats.emit(null);
  }
}
