import {
  AfterContentChecked,
  ChangeDetectorRef,
  Component, EventEmitter, HostListener,
  Input,
  OnChanges,
  OnInit, Output,
  SimpleChanges
} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {GlobalService} from "../../common/services/global.service";

declare var $bean;
declare var toastr;
declare var $;

@Component({
  selector: 'box-list-messenger',
  templateUrl: './box-list-messenger.component.html',
  styleUrls: ['./box-list-messenger.component.css']
})
export class BoxListMessengerComponent implements OnInit, OnChanges {
  @Input() userLogin: any;
  CONNECTION_STATUS_NOT_CONNECT = 'NOT_CONNECTED';
  CONNECTION_STATUS_CONNECT = 'CONNECTED';
  TYPE_MESSENGER_TEXT = 'TEXT';
  TYPE_MESSENGER_IMAGE = 'IMAGE';
  TYPE_MESSENGER_LINK = 'LINK';
  TYPE_MESSENGER_FILE = 'FILE';
  TYPE_ROLE_PRIMARY = 'PRIMARY';
  STATUS_EDITED = 'EDITED';
  ROLE_IS_USER = 'USER';
  ROLE_IS_CHANNEL = 'CHANNEL';
  ROLE_IS_MESSENGER = 'MESSENGER';
  TYPE_CHAT_CONTACT = 'CHAT_CONTACT';
  CLASS_BOX_ACTIVITIES_MESSENGER = 'box-activities-messenger';
  // CLASS_BOX_REACT_EMOJIS = 'box-emojis-react-message';
  CLASS_BOX_REACT_EMOJIS = 'box-emojis-react-messengers';
  CLASS_BOX_MESSAGE_EMOJIS = 'emoji-mart';
  DEFAULT_NUMBER_MESSAGE = 50;
  DEFAULT_OLD_NUMBER_MESSAGE = 5;
  DEFAULT_NUMBER_OFSET = 0;
  selectedChat: any = {};
  requestChat: any = {};
  firstAccess: boolean; // trang thai khi nguoi dung moi truy cap
  connectionStatus = {status: this.CONNECTION_STATUS_CONNECT};
  listMessengers: any;
  countPreviousMessengers: number = 0;
  moreMessage: any = [];
  linkUsersChat: any = [];
  selectedMessenger: any = {};
  listEmojis: any = [];
  setUpActivitiesObj = {
    edit: false,
    copy: false,
    download: false,
    react: false,
    forward: false,
    remove: false
  };
  styleBoxMessageActivities = {
    top: -1000,
    left: -1000
  };
  styleBoxReactEmojis = {
    top: -1000,
    left: -1000
  };
  styleBoxMessageEmojis = {
    top: -1000,
    left: -1000
  };
  selectedMessageElementId: any = '';
  dataShowReactEmoji: any = {};
  dataShowUsersReadMessenger: any = {};
  toastr = toastr;

  // baseUrl = "https://chat.hyper.com:9999/";
  baseUrl = "http://chat.hyper.com:9000/";

  constructor(private cdRef: ChangeDetectorRef, private http: HttpClient, private globalService: GlobalService, private router: Router) {
  }

  @HostListener('document:click', ['$event'])
  checkClick(event) {

    this.hideBoxReactEmojis();

    this.hideBoxMessageActivities();
  }

  ngOnInit() {
    this.firstAccess = true;
    this.getEmojisDefault();
    this.globalService.accessChat.subscribe(res => {
      console.log('this is the current value', res);
      this.firstAccess = false;
      this.selectedChat = res;
      this.listMessengers = [];
      if (res['role'] == this.ROLE_IS_CHANNEL) {
        if (this.selectedChat['type'] == this.TYPE_CHAT_CONTACT) {
          this.selectedChat = res['userPartner'];
        }
      }
      this.goCheckLinkChat();
    });

    this.globalService.hasNewMessenger.subscribe(res => {
      this.notifiedNewMessenger(res);
    });

    this.globalService.hasUpdatedMessenger.subscribe(res => {
      this.notifiedUpdateMessenger(res);
    });

    this.globalService.hasDeletedMessenger.subscribe(res => {
      this.notifiedDeleteMessenger(res);
    });

    this.globalService.hasNewUserChannel.subscribe(res => {
      this.notifiedNewUserChannel(res);
    });

    this.globalService.hasUpdatedUserChannel.subscribe(res => {
      this.notifiedUpdateUserChannel(res);
    });

    this.globalService.hasDeletedUserChannel.subscribe(res => {
      this.notifiedDeleteUserChannel(res);
    });

    this.globalService.hasNewReact.subscribe(res => {
      this.notifiedNewReact(res);
    });

    this.globalService.hasDeletedReact.subscribe(res => {
      this.notifiedDeleteReact(res);
    });


    this.globalService.dataShowReactEmoji.subscribe(res => {
      if ($bean.isNotEmpty(res)) {
        this.dataShowReactEmoji = res;
      } else {
        this.dataShowReactEmoji = {};
      }
    });

    this.globalService.dataShowUsersReadMessenger.subscribe(res => {
      if ($bean.isNotEmpty(res)) {
        this.dataShowUsersReadMessenger = res;
      } else {
        this.dataShowUsersReadMessenger = {};
      }
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  getEmojisDefault() {
    let url = this.baseUrl + 'emojis';
    this.http.get(url).subscribe(emojis => {
      this.listEmojis = emojis;
    })
  }

  goCheckLinkChat() {
    if (this.selectedChat['role'] == this.ROLE_IS_CHANNEL) {
      this.getInfoToChannel(this.selectedChat['id']);
    } else {
      this.getInfoToContact(this.selectedChat['id']);
    }
  }

  getInfoToChannel(channelId) {
    // kiểm tra liên kết user - channel
    let url = this.baseUrl + 'users/checkLinkToChannel';
    let inputChannel = {
      channelId: channelId
    }
    this.http.post(url, inputChannel).subscribe(userChannel => {
      if ($bean.isNotEmpty(userChannel)) {
        this.connectionStatus.status = this.CONNECTION_STATUS_CONNECT;
        this.requestChat = userChannel;
        this.loadExampleMessengers(channelId, this.DEFAULT_OLD_NUMBER_MESSAGE, this.DEFAULT_NUMBER_MESSAGE, userChannel['position']);
        this.getLinkUsersByChat(this.requestChat['channelId']);
      } else {
        this.requestChat = {};
      }
      this.changeFocusChat();
    })
  }

  getInfoToContact(contactId) {
    let url = this.baseUrl + "users/checkLinkToContact";
    let form = {
      contactId: contactId
    }
    this.http.post(url, form).subscribe(userChannel => {
      if ($bean.isNotEmpty(userChannel)) {
        this.connectionStatus.status = this.CONNECTION_STATUS_CONNECT;
        this.requestChat = userChannel;
        this.loadExampleMessengers(userChannel['channelId'], this.DEFAULT_OLD_NUMBER_MESSAGE, this.DEFAULT_NUMBER_MESSAGE, userChannel['position']);
        this.getLinkUsersByChat(this.requestChat['channelId']);
      } else {
        this.requestChat = {};
        this.connectionStatus = {
          status: this.CONNECTION_STATUS_NOT_CONNECT,
        };
      }
      this.changeFocusChat();
    })
  }

  changeFocusChat() {
    let dataChat = {
      requestChat: this.requestChat,
      selectedChat: this.selectedChat
    }
    this.globalService.changeFocusChat.next(dataChat);
  }

  getLinkUsersByChat(channelId) {
    let url = this.baseUrl + 'userChannels/extraInfoUserByChannel';
    let form = {
      channelId: channelId
    }
    this.http.post(url, form).subscribe(data => {
      this.linkUsersChat = data;
    })
  }

  loadExampleMessengers(channelId, oldMessengers, newMessengers, offset) {
    let url = this.baseUrl + 'messengers/exampleMessengers';
    let form = {
      channelId: channelId,
      oldMessengers: oldMessengers,
      newMessengers: newMessengers,
      offset: offset
    }
    this.http.post(url, form).subscribe(data => {
      this.listMessengers = [];
      if ($bean.isNotEmpty(data)) {
        if ($bean.isNotEmpty(data['oldMessengers'])) {
          this.listMessengers = data['oldMessengers'];
          this.countPreviousMessengers = data['oldMessengers'].length;
        }
        if ($bean.isNotEmpty(data['newMessengers'])) {
          if ($bean.isEmpty(this.listMessengers)) {
            this.listMessengers = data['newMessengers'];
          } else {
            for (let i = 0; i < data['newMessengers'].length; i++) {
              this.listMessengers.push(data['newMessengers'][i]);
            }
          }
          //  Cập nhật request Chat
          this.requestChat['position'] += data['newMessengers'].length;
          this.requestChat['lastMessengerId'] = this.listMessengers[this.listMessengers.length - 1].id;
          this.updateStatusUserChat();
        }
      }
    })
  }

  checkLoadMessengers() {
    let url = this.baseUrl + 'users/checkLoadMessengers';
    let form = {
      channelId: this.requestChat['channelId']
    }
    this.http.post(url, form).subscribe(data => {
      if ($bean.isNotEmpty(data)) {
        if (data['countMessengers'] > data['position']) {
          let loadNumber = (data['countMessengers']) - data['position'];
          this.loadMoreMessenger(loadNumber, data['position']);
        }
      }
    })
  }

  dropIntoBoxMessage(ev) {
    ev.preventDefault();
    let files = ev.dataTransfer.files;
    if ($bean.isNotEmpty(files)) {
      let url = this.baseUrl + 'upload';
      for (let i = 0; i < files.length; i++) {
        const formData = new FormData();
        formData.append('channelId', this.requestChat['channelId']);
        formData.append('fileId', $bean.genRandomID(16));
        formData.append('file', files[i]);
        this.http.post(url, formData).subscribe(data => {
          console.log(data);
        })
      }
    }
  }

  checkScrollMessengers() {
    const containerMessenger = document.getElementById('box-list-messenger');
    var y = containerMessenger.scrollTop;
    var innerHeight = containerMessenger.clientHeight;
    var scrollHeight = containerMessenger.scrollHeight;
    if (y == 0) {
      this.loadPreviousMessenger(this.DEFAULT_NUMBER_MESSAGE, this.requestChat['position'] - this.countPreviousMessengers);
    }
    if ((y + innerHeight) == (scrollHeight)) {
      this.loadMoreMessenger(this.DEFAULT_NUMBER_MESSAGE, this.requestChat['position']);
    }
  }

  loadPreviousMessenger(number, offset) {
    const formData = {
      channelId: this.requestChat['channelId'],
      number: number,
      offset: offset
    };
    const url = this.baseUrl + 'messengers/previousMessengers';
    this.http.post(url, formData).subscribe(data => {
      if ($bean.isNotNil(data)) {
        this.moreMessage = data;
        if ($bean.isNotEmpty(this.moreMessage)) {
          console.log(this.moreMessage);
          this.countPreviousMessengers += this.moreMessage.length;
          // Mark phần tử để scroll xuống sau khi load thêm dữ liệu
          for (let message of this.moreMessage) {
            this.listMessengers.splice(0, 0, message);
          }
          this.scrollToElement(this.listMessengers[this.listMessengers.length - this.moreMessage.length].id);
        }
      }
    }, error => {
      console.log('Something went wrong ', error);
    });
  }

  loadMoreMessenger(number, offset) {
    const formData = {
      channelId: this.requestChat['channelId'],
      number: number,
      offset: offset
    };
    const url = this.baseUrl + 'messengers/moreMessengers';
    this.http.post(url, formData).subscribe(data => {
      if ($bean.isNotNil(data)) {
        this.moreMessage = data;
        if ($bean.isNotEmpty(this.moreMessage)) {
          console.log(this.moreMessage);
          for (let message of this.moreMessage) {
            this.listMessengers.push(message);
          }
          this.moveToLastMessage();
          //  Cập nhật request Chat
          this.requestChat['position'] += this.moreMessage.length;
          this.requestChat['lastMessengerId'] = this.moreMessage[this.moreMessage.length - 1].id;
          this.updateStatusUserChat();
        }
      }
    }, error => {
      console.log('Something went wrong ', error);
    });
  }


// Scroll xuống message cuối cùng trên kênh
  moveToLastMessage() {
    this.scrollToElement(this.listMessengers[this.listMessengers.length - 1].id);
  }

// Scroll xuống vị trí được chỉ định
  scrollToElement(idElement) {
    const element = document.getElementById(idElement);
    if ($bean.isNotNil(element)) {
      element.scrollIntoView(false);
    }
  }

  allowDrop(ev, showBorder) {
    ev.preventDefault();
    if (showBorder) {
      event.target['style'].border = "4px dotted green";
    }
  }

  sendRequest() {
    if (this.selectedChat['role'] == this.ROLE_IS_USER) {
      this.addContact();
    }
  }

  addContact() {
    let url = this.baseUrl + 'channels/addContact';
    let form = {
      members: [this.selectedChat.id]
    };
    this.http.post(url, form).subscribe(data => {
      this.connectionStatus.status = this.CONNECTION_STATUS_CONNECT;
      this.getInfoToChannel(data['id']);
    })
  }

  showBoxMessageActivities(messenger, triggerId, event) {
    this.selectedMessenger = messenger;
    console.log('Selected messenger');
    console.log(this.selectedMessenger);
    this.setUpActivites(messenger);
    let triggerElement = $('#' + triggerId);
    let triggerTop = triggerElement.offset().top;
    let triggerLeft = triggerElement.offset().left;
    let outerWidthTrigger = triggerElement.outerWidth();
    let outerHeightTrigger = triggerElement.outerHeight();
    let activitiesMessageBox = $('.' + this.CLASS_BOX_ACTIVITIES_MESSENGER);
    let outerWidthActivitiesMessageBox = activitiesMessageBox.outerWidth();
    let outerHeightActivitiesMessageBox = activitiesMessageBox.outerHeight();
    let activitiesMessageBoxTop = triggerTop - outerHeightActivitiesMessageBox / 2;
    let activitiesMessageBoxLeft = triggerLeft - outerWidthTrigger - outerWidthActivitiesMessageBox - 10;
    this.styleBoxMessageActivities.top = activitiesMessageBoxTop;
    this.styleBoxMessageActivities.left = activitiesMessageBoxLeft;
    console.log(this.styleBoxMessageActivities);
    event.stopPropagation();
  }

  setUpActivites(messenger) {
    if ($bean.isNotNil(messenger.path)) {
      this.setUpActivitiesObj.download = true;
    }
  }

  showBoxReactEmojis(event, triggerId, messenger) {
    this.showBoxEmojis(event, triggerId, this.CLASS_BOX_REACT_EMOJIS);
    this.selectedMessenger = messenger;
  }

  showBoxEmojis(event, triggerId, classBoxEmoji) {
    let triggerElement = $('#' + triggerId);
    let triggerTop = triggerElement.offset().top;
    let triggerLeft = triggerElement.offset().left;
    let outerWidthTrigger = triggerElement.outerWidth();
    let outerHeightTrigger = triggerElement.outerHeight();
    let emojisBox = $('.' + classBoxEmoji);
    let outerWidthEmojisBox = emojisBox.outerWidth();
    let outerHeightEmojisBox = emojisBox.outerHeight();
    let emojisBoxTop = triggerTop - 10 - outerHeightEmojisBox;
    let emojesBoxLeft = triggerLeft + outerWidthTrigger / 2 - outerWidthEmojisBox / 2;
    if (classBoxEmoji == this.CLASS_BOX_MESSAGE_EMOJIS) {
      this.styleBoxMessageEmojis.top = emojisBoxTop;
      this.styleBoxMessageEmojis.left = emojesBoxLeft;
      console.log(this.styleBoxMessageEmojis);
    } else {
      this.styleBoxReactEmojis.top = emojisBoxTop;
      this.styleBoxReactEmojis.left = emojesBoxLeft;
      console.log(this.styleBoxReactEmojis);
    }
    event.stopPropagation();
  }

  hideBoxMessageEmojis() {
    this.styleBoxMessageEmojis.top = -1000;
    this.styleBoxMessageEmojis.left = -1000;
  }

  hideBoxReactEmojis() {
    this.styleBoxReactEmojis.top = -1000;
    this.styleBoxReactEmojis.left = -1000;
  }

  hideBoxMessageActivities() {
    this.styleBoxMessageActivities.top = -1000;
    this.styleBoxMessageActivities.left = -1000;
  }

  updateMessenger() {
    this.globalService.userUpdateMessenger.next(this.selectedMessenger);
  }

  forwardMessenger() {
    this.globalService.hasForwardMessenger.next(this.selectedMessenger);
    $('#modalBoxForward').modal('show');
  }

  copyMessenger() {
    this.selectedMessageElementId = 'item-message' + '-' + this.selectedMessenger.id;
    if ($bean.isNotEmpty(this.selectedMessageElementId)) {
      $('#inputCopy').val($('#' + this.selectedMessageElementId).html());
      $('#inputCopy').select();
      document.execCommand("copy");
      alert("Copied the text: " + $('#inputCopy').val());
    } else {
      alert('Ban chua chon message !');
    }
  }

  removeMessenger() {
    let url = this.baseUrl + 'messengers/delete';
    let form = {
      messengerId: this.selectedMessenger.id
    }
    this.http.post(url, form).subscribe(data => {
      console.log('Delete messenger');
      console.log(data);
    })
  }

  refreshReactMessenger(reacts) {
    let cloneReacts = $bean.clone(reacts);
    return cloneReacts;
  }

  reactMessenger(emoji) {
    if ($bean.isNotEmpty(this.selectedMessenger['userMessengers'])) {
      let reactMessengers = this.selectedMessenger['userMessengers'];
      let found = false;
      for (let i = 0; i < reactMessengers.length; i++) {
        if ((reactMessengers[i].userId == this.userLogin.id) && (reactMessengers[i].emoji == emoji.native)) {
          this.deleteReactMessenger(reactMessengers[i]);
          found = true;
          break;
        }
      }
      if (!found) {
        this.newReactMessenger(emoji);
      }
    } else {
      this.newReactMessenger(emoji);
    }
  }

  newReactMessenger(emoji) {
    console.log(emoji);
    let url = this.baseUrl + 'userMessengers/reactMessenger';
    let form = {
      userId: this.userLogin.id,
      messengerId: this.selectedMessenger.id,
      channelId: this.selectedMessenger.channelId,
      emoji: emoji.native
    }
    this.http.post(url, form).subscribe(data => {
      console.log('React message ');
      console.log(data);
    })
  }

  deleteReactMessenger(react) {
    let url = this.baseUrl + 'userMessengers/deleteReact';
    let form = {
      userMessengerId: react.id
    }
    this.http.post(url, form).subscribe(data => {
      console.log('Delete react ');
      console.log(data);
    })
  }

  updateLinkUserChannel(userChannel) {
    if ($bean.isNotEmpty(this.linkUsersChat)) {
      for (let i = 0; i < this.linkUsersChat.length; i++) {
        if (this.linkUsersChat['id'] == userChannel.id) {
          this.linkUsersChat[i] = userChannel;
        }
      }
    }
  }

  deleteLinkUserChannel(id) {
    if ($bean.isNotEmpty(this.linkUsersChat)) {
      for (let i = 0; i < this.linkUsersChat.length; i++) {
        if (this.linkUsersChat['id'] == id) {
          this.linkUsersChat.splice(i, 1);
        }
      }
    }
  }

  addLinkUserChannel(userChannel) {
    if ($bean.isNotEmpty(this.linkUsersChat)) {
      this.linkUsersChat.push(userChannel);
    } else {
      this.linkUsersChat = [];
      this.linkUsersChat.push(userChannel);
    }
  }

  notifiedNewMessenger(data) {
    if ($bean.isNotEmpty(data)) {
      if (data['channelId'] == this.requestChat['channelId']) {
        // if (data['value']['oldCountMessengers'] == this.requestChat['position']) {
        this.listMessengers.push(data['value']);
        this.requestChat['position']++;
        this.requestChat['lastMessengerId'] = data['value'].id;
        // this.updateStatusUserChat();
        // } else {
        //   this.loadMoreMessenger(data['value']['oldCountMessengers'] + 1 - this.requestChat['position'], this.requestChat['position']);
        // }
      } else {
        if ($bean.isNotEmpty(data['value'].channelTitle)) {
          toastr.info(data['value'].message, data['value']['user'].username);
        } else {
          toastr.info(data['value'].message, data['value']['user'].username + ' - ' + data['value']['channelTitle']);
        }
      }
    }
  }

  notifiedUpdateMessenger(data) {
    if ($bean.isNotEmpty(data) && (data['channelId'] == this.requestChat['channelId']) && $bean.isNotEmpty(this.listMessengers)) {
      for (let i = 0; i < this.listMessengers.length; i++) {
        if (this.listMessengers[i].id == data['value'].id) {
          this.listMessengers[i] = data['value'];
          break;
        }
      }
    }
  }

  notifiedDeleteMessenger(data) {
    if ($bean.isNotEmpty(data) && (data['channelId'] == this.requestChat['channelId']) && $bean.isNotEmpty(this.listMessengers)) {
      for (let i = 0; i < this.listMessengers.length; i++) {
        if (this.listMessengers[i].id == data['value'].id) {
          this.listMessengers.splice(i, 1);
          break;
        }
      }
    }
  }

  notifiedNewReact(data) {
    if ($bean.isNotEmpty(data)) {
      if ((data['channelId'] == this.requestChat['channelId']) && $bean.isNotEmpty(this.listMessengers)) {
        for (let i = 0; i < this.listMessengers.length; i++) {
          if (this.listMessengers[i].id == data.messengerId) {
            if ($bean.isEmpty(this.listMessengers[i]['userMessengers'])) {
              this.listMessengers[i]['userMessengers'] = [];
            }
            this.listMessengers[i]['userMessengers'].push(data.value);
            this.listMessengers[i]['userMessengers'] = this.refreshReactMessenger(this.listMessengers[i]['userMessengers']);
            break;
          }
        }
      } else {
        alert(data['value']);
      }
    }
  }

  notifiedDeleteReact(data) {
    if ($bean.isNotEmpty(data)) {
      if ((data['channelId'] == this.requestChat['channelId']) && $bean.isNotEmpty(this.listMessengers)) {
        for (let i = 0; i < this.listMessengers.length; i++) {
          if (this.listMessengers[i].id == data.messengerId) {
            if ($bean.isNotEmpty(this.listMessengers[i]['userMessengers'])) {
              for (let j = 0; j < this.listMessengers[i]['userMessengers'].length; j++) {
                if (this.listMessengers[i]['userMessengers'][j].id == data.value.id) {
                  this.listMessengers[i]['userMessengers'].splice(j, 1);
                  this.listMessengers[i]['userMessengers'] = this.refreshReactMessenger(this.listMessengers[i]['userMessengers']);
                  break;
                }
              }
            }
          }
        }
      }
    }
  }

  notifiedNewUserChannel(data) {
    if ($bean.isNotEmpty(data)) {
      if ((data['value']['channelId'] == this.requestChat['channelId'])) {
        this.linkUsersChat.push(data['value']);
        this.linkUsersChat = this.refreshObj(this.linkUsersChat);
      }
    }
  }

  notifiedUpdateUserChannel(data) {
    if ($bean.isNotEmpty(data)) {
      if ((data['channelId'] == this.requestChat['channelId']) && ($bean.isNotEmpty(this.linkUsersChat))) {
        for (let i = 0; i < this.linkUsersChat.length; i++) {
          if (this.linkUsersChat[i].id == data['value']['id']) {
            // this.linkUsersChat[i] = data['value'];
            for (let key in data['value']) {
              this.linkUsersChat[i][key] = data['value'][key];
            }
            this.linkUsersChat = this.refreshObj(this.linkUsersChat);
          }
        }
      }
    }
  }

  notifiedDeleteUserChannel(data) {
    if ($bean.isNotEmpty(data)) {
      if (this.requestChat.id != data['value'].id) {
        if ((data['channelId'] == this.requestChat['channelId']) && ($bean.isNotEmpty(this.linkUsersChat))) {
          for (let i = 0; i < this.linkUsersChat.length; i++) {
            if (this.linkUsersChat[i].id == data['value']['id']) {
              this.linkUsersChat.splice(i, 1);
              this.linkUsersChat = this.refreshObj(this.linkUsersChat);
            }
          }
        }
      } else {
        this.firstAccess = true;
      }
    }
  }

  isSeperateMessenger(preDate, nextDate) {
    let fiveMininutes = 5 * 60 * 1000;
    let preDateTimes = new Date(preDate).getTime();
    let nextDateTimes = new Date(nextDate).getTime();
    if ((nextDateTimes - preDateTimes) > fiveMininutes) {
      return true;
    }
    return false;
  }

  isSepearteDate(date, otherDate) {
    let preDate = new Date(date);
    let nextDate = new Date(otherDate);
    if ((preDate.getFullYear() == nextDate.getFullYear()) && (preDate.getMonth() == nextDate.getMonth()) && (preDate.getDate() == nextDate.getDate())) {
      return false;
    }
    return true;
  }

  updateStatusUserChat() {
    let url = this.baseUrl + 'userChannels/updateViewMessengers';
    let form = this.requestChat;
    this.http.post(url, form).subscribe(data => {
      if ($bean.isNotEmpty(data)) {
        console.log('Update Status User Chat');
        console.log(data);
      }
    })
  }

  linkExternalFile(path) {
    let url = this.baseUrl + path;
    window.open(url, "_blank");
  }

  refreshObj(obj) {
    let newObj = $bean.clone(obj);
    return newObj;
  }

}
