import {
  AfterContentChecked,
  ChangeDetectorRef,
  Component, EventEmitter,
  Input,
  OnChanges,
  OnInit, Output,
  SimpleChanges
} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';

declare var $bean;
declare var $;

@Component({
  selector: 'box-info-chat',
  templateUrl: './box-info-chat.component.html',
  styleUrls: ['./box-info-chat.component.css']
})
export class BoxInfoChatComponent implements OnInit, OnChanges {

  @Input() chat: any;
  @Input() unReadMessengers: number;
  @Output() callBack: EventEmitter<any> = new EventEmitter();

  constructor(private cdRef: ChangeDetectorRef) {
  }

  ngOnInit() {
    if ($bean.isNil(this.chat)) {
      this.chat = {}
    }

    if ($bean.isNil(this.unReadMessengers)) {
      this.unReadMessengers = 0;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  chooseChat() {
    this.callBack.emit(this.chat);
  }
}
