import {Component, Input, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {GlobalService} from "../../common/services/global.service";

declare var $bean;
declare var $;

@Component({
  selector: 'box-side-bar',
  templateUrl: './box-side-bar.component.html',
  styleUrls: ['./box-side-bar.component.css']
})
export class BoxSideBarComponent implements OnInit {

  @Input() userLogin: any;
  LOCATION_CHATS = 'LOCATION_CHATS';
  LOCATION_CONTACTS = 'LOCATION_CONTACTS';
  LOCATION_HOME_NOTIFICATIONS = 'LOCATION_HOME_NOTIFICATIONS';
  DEFAULT_NUMBER_USER = 20;
  DEFAULT_NUMBER_CHAT = 5;
  DEFAULT_NUMBER_OFSET = 0;
  // Common
  // baseUrl = "https://chat.hyper.com:9999/";
  baseUrl = "http://chat.hyper.com:9000/";
  searchValue: string = '';
  isSearching: boolean = false;
  location: string = this.LOCATION_CHATS;

  // Contacts
  suggestedContacts: any = [];
  availbleContacts: number = 0;

  // Chats
  suggestedChats: any = [];
  availbleChats: number = 0;
  modifiedChats: any = [];
  statusUserChannels: any = {};
  unreadChats: number = 0;
  numberLoadUserChat = 0;
  isArrangeChat: boolean = false;
  requestChat: any = {};

  constructor(private http: HttpClient, private globalService: GlobalService, private router: Router) {

  }

  ngOnInit() {
    this.location = this.LOCATION_CHATS;
    this.getChannels(this.DEFAULT_NUMBER_CHAT);

    this.globalService.changeFocusChat.subscribe(res => {
      if ($bean.isNotEmpty(res)) {
        this.requestChat = res['requestChat'];
      } else {
        this.requestChat = {};
      }
    })

    this.globalService.getMoreChats.subscribe(res => {
      this.getMoreChats();
    });

    this.globalService.hasNewMessenger.subscribe(res => {
      this.notifiedNewMessenger(res);
    });

    this.globalService.hasUpdatedMessenger.subscribe(res => {
      this.notifiedUpdateMessenger(res);
    });

    this.globalService.hasDeletedMessenger.subscribe(res => {
      this.notifiedDeleteMessenger(res);
    });

    this.globalService.hasNewUserChannel.subscribe(res => {
      this.notifiedNewUserChannel(res);
    });

    this.globalService.hasUpdatedUserChannel.subscribe(res => {
      this.notifiedUpdateUserChannel(res);
    });

    this.globalService.hasDeletedUserChannel.subscribe(res => {
      this.notifiedDeleteUserChannel(res);
    });

  }

  goToChats() {
    if (this.location == this.LOCATION_CONTACTS) {
      this.location = this.LOCATION_CHATS;
      this.resetViewChat();
      this.getChannels(this.DEFAULT_NUMBER_CHAT);
    }
  }

  goToContacts() {
    if (this.location == this.LOCATION_CHATS) {
      this.location = this.LOCATION_CONTACTS;
      this.listContacts(this.DEFAULT_NUMBER_USER, this.DEFAULT_NUMBER_OFSET);
    }
  }

  goToNotifications() {
    this.location = this.LOCATION_HOME_NOTIFICATIONS;
  }

  showModalInitAddChannel() {
    $('#modalInitAddChannel').modal('show');
    // reset lại giá trị của đối tượng newChannel
    this.globalService.addChannel.next({});
  }

  changeActivity() {
    this.isSearching = true;
  }

  closeSearch() {
    this.isSearching = false;
    this.searchValue = '';
    this.getChannels(this.DEFAULT_NUMBER_CHAT);
  }

  logout() {
    const url = this.baseUrl + 'logout';
    this.http.get(url).subscribe(data => {
      document.location.href = this.baseUrl;
    });
  }

  showProfile() {
    $('#modalViewChatmeProfile').modal('show');
  }

  viewChatmeProfile() {

  }

//  Làm chức năng thực thi với Chats

  resetViewChat() {
    this.suggestedChats = [];
    this.unreadChats = 0;
    this.statusUserChannels = {};
    this.numberLoadUserChat = 0;
    this.modifiedChats = [];
  }

  getChannels(number) {
    let url = this.baseUrl + 'channels/listByUser';
    let form = {
      number: number
    }
    this.http.post(url, form).subscribe(data => {
      this.resetViewChat();
      if ($bean.isNotEmpty(data)) {
        this.suggestedChats = data['channels'];
        this.availbleChats = data['availbleNumber'];
        console.log('My Channels');
        console.log(this.suggestedChats);
        if ($bean.isNotEmpty(this.suggestedChats)) {
          this.modifiedChats = [];
          for (let i = 0; i < this.suggestedChats.length; i++) {
            let objModified = this.modifiedViewChannel(this.suggestedChats[i]);
            this.modifiedChats.push(objModified);
            this.getStatusUserChat(this.suggestedChats[i].id);
          }
          this.sortChatsByTimeWorking(this.modifiedChats);
          this.suggestedChats = this.modifiedChats;
          this.changeSuggestedChats();
        }
      }
    })
  }

  searchChannels(number, value) {
    let url = this.baseUrl + 'channels/searchChannels';
    let form = {
      number: number,
      value: value
    }
    this.http.post(url, form).subscribe(data => {
      this.resetViewChat();
      if ($bean.isNotEmpty(data)) {
        this.suggestedChats = data['channels'];
        this.availbleChats = data['availbleNumber'];
        console.log('My Channels');
        console.log(this.suggestedChats);
        if ($bean.isNotEmpty(this.suggestedChats)) {
          this.modifiedChats = [];
          for (let i = 0; i < this.suggestedChats.length; i++) {
            let objModified = this.modifiedViewChannel(this.suggestedChats[i]);
            this.modifiedChats.push(objModified);
            this.getStatusUserChat(this.suggestedChats[i].id);
          }
          this.sortChatsByTimeWorking(this.modifiedChats);
          this.suggestedChats = this.modifiedChats;
          this.changeSuggestedChats();
        }
      }
    })
  }

  moreChannels() {
    this.getChannels(this.suggestedChats.length + this.DEFAULT_NUMBER_CHAT);
  }

  searchMore() {
    this.searchChannels(this.suggestedChats.length + this.DEFAULT_NUMBER_CHAT, this.searchValue);
  }


  modifiedViewChannel(viewChannel) {
    let objModified = $bean.clone(viewChannel);
    if ($bean.isNil(objModified['title'])) {
      objModified['title'] = viewChannel['users'][0].username;
    }
    objModified['userPartner'] = viewChannel['users'][0];
    delete objModified['users'];
    objModified['lastMessengerId'] = viewChannel['messengers'][0].id;
    objModified['lastMessage'] = viewChannel['messengers'][0].message;
    objModified['lastWorkingDate'] = new Date(viewChannel['messengers'][0].updatedAt).getTime();
    objModified['dateFormat'] = this.getFormateDate(new Date(viewChannel['messengers'][0].updatedAt).getTime());
    return objModified;
  }

  sortChatsByTimeWorking(chats) {
    if ($bean.isNotEmpty(chats)) {
      chats.sort(function (a, b) {
        return b['lastWorkingDate'] - a['lastWorkingDate'];
      })
      console.log('Chats after sort');
      console.log(chats);
    }
  }

  getFormateDate(inputTimes) {
    let inputDate = new Date(inputTimes);
    let today = new Date();
    if ((inputDate.getFullYear() == today.getFullYear()) && (inputDate.getMonth() == today.getMonth()) && (inputDate.getDate() == today.getDate())) {
      return 'shortTime';
    } else {
      return 'mediumDate';
    }
  }

  getMoreChats() {
    if ($bean.isNotEmpty(this.searchValue)) {
      this.searchMore();
    } else {
      this.moreChannels();
    }
  }

  getChat(channelId) {
    let url = this.baseUrl + 'channels/viewChannel';
    let form = {
      channelId: channelId
    }
    this.http.post(url, form).subscribe(data => {
      if ($bean.isNotEmpty(data)) {
        let found = false;
        data = this.modifiedViewChannel(data);
        if ($bean.isNotEmpty(this.suggestedChats)) {
          for (let i = 0; i < this.suggestedChats.length; i++) {
            if (this.suggestedChats[i].id == data['id']) {
              found = true;
              this.suggestedChats[i] = data;
              break;
            }
          }
        } else {
          this.suggestedChats = [];
        }
        if (!found) {
          this.suggestedChats.splice(0, 0, data);
        }
        this.getStatusUserChat(data['id']);
        this.changeSuggestedChats();
      }
    })
  }

  getStatusUserChat(channelId) {
    let url = this.baseUrl + 'channels/statusUserChannel';
    let form = {
      channelId: channelId
    }
    this.http.post(url, form).subscribe(data => {
      if ($bean.isNotEmpty(data)) {
        this.statusUserChannels[data['channelId']] = data['unReadMessengers'];
        if (this.numberLoadUserChat < this.suggestedChats.length) {
          this.numberLoadUserChat++;
        }
        if (this.numberLoadUserChat >= this.suggestedChats.length) {
          this.countUnreadChats();
        }
      }
    })
  }

  countUnreadChats() {
    this.unreadChats = 0;
    if ($bean.isNotEmpty(this.statusUserChannels)) {
      for (let key in this.statusUserChannels) {
        let unReadMessengers = this.statusUserChannels[key];
        if ((unReadMessengers) > 0) {
          this.unreadChats++;
        }
      }
    }
  }

//  Các chức năng thực thi với Contacts

  // countAllUsers() {
  //   let url = this.baseUrl + 'users/countAll';
  //   let form = {};
  //   this.http.post(url, form).subscribe(data => {
  //     this.totalContacts = data['count'];
  //   })
  // }

  listContacts(number, offset) {
    const url = this.baseUrl + 'users/suggested';
    let form = {
      number: number,
      offset: offset
    }
    this.http.post(url, form).subscribe(data => {
      if ($bean.isNotEmpty(data)) {
        this.suggestedContacts = data;
      }
    });
  }

  getMoreContacts(number, offset) {
    const url = this.baseUrl + 'users/suggested';
    let form = {
      number: number,
      offset: offset
    }
    this.http.post(url, form).subscribe(data => {
      if ($bean.isNotEmpty(data)) {
        let tempData: any = [];
        tempData = data;
        for (let i = 0; i < tempData.length; i++) {
          this.suggestedContacts.push(tempData[i]);
        }
      }
    });
  }

  notifiedNewMessenger(data) {
    if ($bean.isNotEmpty(data)) {
      let found = false;
      if ($bean.isNotEmpty(this.suggestedChats)) {
        for (let i = 0; i < this.suggestedChats.length; i++) {
          if (this.suggestedChats[i].id == data['channelId']) {
            found = true;
            this.isArrangeChat = true;
            this.suggestedChats[i]['lastMessengerId'] = data.value.id;
            this.suggestedChats[i]['lastMessage'] = data.value.message;
            this.suggestedChats[i]['lastWorkingDate'] = new Date(data.value.createdAt).getTime();
            this.suggestedChats[i]['dateFormat'] = this.getFormateDate(new Date(data.value.createdAt).getTime());
            this.statusUserChannels[data['channelId']]++;
            if ($bean.isNotEmpty(this.requestChat) && (data['channelId'] != this.requestChat.channelId) && (i != 0)) {
              let infoChat = this.suggestedChats[i];
              this.suggestedChats.splice(i, 1);
              this.suggestedChats.splice(0, 0, infoChat);
            }
            this.countUnreadChats();
            break;
          }
        }
      }
      if (!found) {
        this.getChat(data['channelId']);
      }
    }
  }

  notifiedUpdateMessenger(data) {
    if ($bean.isNotEmpty(data) && $bean.isNotEmpty(this.suggestedChats)) {
      for (let i = 0; i < this.suggestedChats.length; i++) {
        if (this.suggestedChats[i].id == data['channelId']) {
          if (this.suggestedChats[i]['lastMessengerId'] == data['value'].id) {
            this.suggestedChats[i]['lastMessage'] = data.value.message;
            this.suggestedChats[i]['lastWorkingDate'] = new Date(data.value.createdAt).getTime();
            this.suggestedChats[i]['dateFormat'] = this.getFormateDate(new Date(data.value.createdAt).getTime());
            break;
          }
        }
      }
    }
  }

  notifiedDeleteMessenger(data) {
    if ($bean.isNotEmpty(data) && $bean.isNotEmpty(this.suggestedChats)) {
      for (let i = 0; i < this.suggestedChats.length; i++) {
        if (this.suggestedChats[i].id == data['channelId']) {
          if (this.suggestedChats[i]['lastMessengerId'] == data['value'].id) {
            // Load lại thông tin kênh chat
            this.getChat(this.suggestedChats[i].id);
            break;
          }
        }
      }
    }
  }

  notifiedNewUserChannel(data) {
    if ($bean.isNotEmpty(data) && (data['value'].userId == this.userLogin['id'])) {
      this.getChat(data['value']['channelId']);
    }
  }

  notifiedUpdateUserChannel(data) {
    if ($bean.isNotEmpty(data) && (data['value'].userId == this.userLogin['id'])) {
      if (this.statusUserChannels.hasOwnProperty(data['channelId'])) {
        this.statusUserChannels[data['channelId']] = data['value']['unReadMessengers'];
        this.countUnreadChats();
      }
    }
  }

  notifiedDeleteUserChannel(data) {
    if ($bean.isNotEmpty(data) && (data['value'].userId == this.userLogin['id']) && ($bean.isNotEmpty(this.suggestedChats))) {
      for (let i = 0; i < this.suggestedChats.length; i++) {
        if (this.suggestedChats[i].id == data['value']['channelId']) {
          this.suggestedChats.splice(i, 1);
          delete this.statusUserChannels[data['value']['channelId']];
          this.countUnreadChats();
          this.changeSuggestedChats();
          break;
        }
      }
    }
  }

  changeSuggestedChats() {
    this.globalService.suggestedChats.next({availbleChats: this.availbleChats, suggestedChats: this.suggestedChats});
  }
}
